"use client"

import { useState, useMemo } from "react"
import Link from "next/link"
import { Header } from "@/components/layout/header"
import { PageHeader } from "@/components/layout/page-header"
import { Button } from "@/components/ui/button"
import { UserFilters, type UserFilterState } from "@/components/users/user-filters"
import { UserStatusBadge } from "@/components/users/user-status-badge"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Pagination } from "@/components/documents/pagination"
import { Plus, MoreVertical, ArrowUpDown, Pencil, Eye, Lock, Trash2 } from "lucide-react"
import { mockUsers } from "@/lib/mock-users"
import { cn } from "@/lib/utils"
import type { User } from "@/types/user"

type SortField = "name" | "role" | "email" | "phone" | "status" | "lastLogin"
type SortDirection = "asc" | "desc"

export default function UsersPage() {
  const [filters, setFilters] = useState<UserFilterState>({
    search: "",
    roles: [],
    statuses: [],
  })
  const [currentPage, setCurrentPage] = useState(1)
  const [pageSize, setPageSize] = useState(10)
  const [sortField, setSortField] = useState<SortField>("name")
  const [sortDirection, setSortDirection] = useState<SortDirection>("asc")

  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortDirection(sortDirection === "asc" ? "desc" : "asc")
    } else {
      setSortField(field)
      setSortDirection("asc")
    }
  }

  const filteredUsers = useMemo(() => {
    return mockUsers.filter((user) => {
      // Search filter
      if (filters.search) {
        const fullName = `${user.lastName} ${user.firstName} ${user.middleName}`
        const matchesSearch =
          fullName.toLowerCase().includes(filters.search.toLowerCase()) ||
          user.email.toLowerCase().includes(filters.search.toLowerCase())
        if (!matchesSearch) return false
      }
      // Role filter
      if (filters.roles.length > 0 && !filters.roles.includes(user.role)) {
        return false
      }
      // Status filter
      if (filters.statuses.length > 0 && !filters.statuses.includes(user.status)) {
        return false
      }
      return true
    })
  }, [filters])

  const sortedUsers = useMemo(() => {
    return [...filteredUsers].sort((a, b) => {
      let aValue: string, bValue: string
      switch (sortField) {
        case "name":
          aValue = `${a.lastName} ${a.firstName} ${a.middleName}`
          bValue = `${b.lastName} ${b.firstName} ${b.middleName}`
          break
        case "role":
          aValue = a.role
          bValue = b.role
          break
        case "email":
          aValue = a.email
          bValue = b.email
          break
        case "phone":
          aValue = a.phone
          bValue = b.phone
          break
        case "status":
          aValue = a.status
          bValue = b.status
          break
        case "lastLogin":
          aValue = a.lastLogin
          bValue = b.lastLogin
          break
        default:
          return 0
      }
      const comparison = aValue.localeCompare(bValue, "ru")
      return sortDirection === "asc" ? comparison : -comparison
    })
  }, [filteredUsers, sortField, sortDirection])

  const totalPages = Math.ceil(sortedUsers.length / pageSize)
  const paginatedUsers = sortedUsers.slice((currentPage - 1) * pageSize, currentPage * pageSize)

  const getInitials = (user: User) => {
    return `${user.lastName[0]}${user.firstName[0]}`
  }

  const SortButton = ({ field, children }: { field: SortField; children: React.ReactNode }) => (
    <button
      className="flex items-center gap-1 text-sm font-medium text-muted-foreground hover:text-foreground transition-colors"
      onClick={() => handleSort(field)}
    >
      {children}
      <ArrowUpDown className={cn("h-3.5 w-3.5", sortField === field && "text-foreground")} />
    </button>
  )

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />

      <PageHeader
        title="Пользователи"
        badge={filteredUsers.length}
        actions={
          <Button size="sm" className="gap-2 bg-primary hover:bg-primary/90">
            <Plus className="h-4 w-4" />
            Добавить пользователя
          </Button>
        }
      />

      <main className="px-6 py-6">
        <div className="mb-6">
          <UserFilters filters={filters} onFiltersChange={setFilters} totalUsers={filteredUsers.length} />
        </div>

        <div className="rounded-lg border bg-white">
          <table className="w-full">
            <thead>
              <tr className="border-b">
                <th className="px-4 py-3 text-left">
                  <SortButton field="name">Пользователь</SortButton>
                </th>
                <th className="px-4 py-3 text-left">
                  <SortButton field="role">Роль</SortButton>
                </th>
                <th className="px-4 py-3 text-left">
                  <SortButton field="email">Email</SortButton>
                </th>
                <th className="px-4 py-3 text-left">
                  <SortButton field="phone">Телефон</SortButton>
                </th>
                <th className="px-4 py-3 text-left">
                  <SortButton field="status">Статус</SortButton>
                </th>
                <th className="px-4 py-3 text-left">
                  <SortButton field="lastLogin">Последний вход</SortButton>
                </th>
                <th className="w-12 px-4 py-3"></th>
              </tr>
            </thead>
            <tbody>
              {paginatedUsers.map((user) => (
                <tr
                  key={user.id}
                  className="border-b last:border-b-0 hover:bg-accent/50 transition-colors cursor-pointer"
                  onClick={() => (window.location.href = `/users/${user.id}`)}
                >
                  <td className="px-4 py-3">
                    <div className="flex items-center gap-3">
                      <span className="flex h-8 w-8 items-center justify-center rounded-full bg-orange-100 text-xs font-medium text-orange-700">
                        {getInitials(user)}
                      </span>
                      <span className="text-sm">
                        {user.lastName} {user.firstName} {user.middleName}
                      </span>
                    </div>
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm">{user.role}</span>
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm">{user.email}</span>
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm">{user.phone}</span>
                  </td>
                  <td className="px-4 py-3">
                    <UserStatusBadge status={user.status} />
                  </td>
                  <td className="px-4 py-3">
                    <span className="text-sm">{user.lastLogin}</span>
                  </td>
                  <td className="px-4 py-3" onClick={(e) => e.stopPropagation()}>
                    <DropdownMenu>
                      <DropdownMenuTrigger asChild>
                        <Button variant="ghost" size="icon" className="h-8 w-8">
                          <MoreVertical className="h-4 w-4" />
                        </Button>
                      </DropdownMenuTrigger>
                      <DropdownMenuContent align="end" className="w-[180px]">
                        <DropdownMenuItem asChild>
                          <Link href={`/users/${user.id}/edit`} className="cursor-pointer">
                            <Pencil className="h-4 w-4 mr-2" />
                            Редактировать
                          </Link>
                        </DropdownMenuItem>
                        <DropdownMenuItem asChild>
                          <Link href={`/users/${user.id}`} className="cursor-pointer">
                            <Eye className="h-4 w-4 mr-2" />
                            Просмотр
                          </Link>
                        </DropdownMenuItem>
                        <DropdownMenuItem>
                          <Lock className="h-4 w-4 mr-2" />
                          Заблокировать
                        </DropdownMenuItem>
                        <DropdownMenuSeparator />
                        <DropdownMenuItem variant="destructive">
                          <Trash2 className="h-4 w-4 mr-2" />
                          Удалить
                        </DropdownMenuItem>
                      </DropdownMenuContent>
                    </DropdownMenu>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        <Pagination
          currentPage={currentPage}
          totalPages={totalPages || 1}
          pageSize={pageSize}
          onPageChange={setCurrentPage}
          onPageSizeChange={(size) => {
            setPageSize(size)
            setCurrentPage(1)
          }}
        />
      </main>
    </div>
  )
}
