"use client"

import { useState, useEffect, useRef } from "react"
import {
  Search,
  Calendar,
  SlidersHorizontal,
  X,
  User,
  CheckCircle,
  ChevronRight,
  ArrowLeft,
  Check,
} from "lucide-react"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover"
import { Calendar as CalendarComponent } from "@/components/ui/calendar"
import { cn } from "@/lib/utils"

export interface UserFilterState {
  search: string
  registrationDate?: Date
  roles: string[]
  statuses: string[]
}

interface UserFiltersProps {
  filters: UserFilterState
  onFiltersChange: (filters: UserFilterState) => void
  totalUsers: number
}

type FilterCategory = "roles" | "statuses"

const filterOptions = {
  roles: ["Спортсмен", "Судья", "Спортивный менеджер", "Администратор"],
  statuses: [
    { value: "active", label: "Активный" },
    { value: "inactive", label: "Неактивный" },
  ],
}

export function UserFilters({ filters, onFiltersChange, totalUsers }: UserFiltersProps) {
  const [isFilterOpen, setIsFilterOpen] = useState(false)
  const [activeSubmenu, setActiveSubmenu] = useState<FilterCategory | null>(null)
  const [submenuSearch, setSubmenuSearch] = useState("")
  const [mainSearch, setMainSearch] = useState("")
  const [isSearchingInSubmenu, setIsSearchingInSubmenu] = useState(false)
  const [isCalendarOpen, setIsCalendarOpen] = useState(false)
  const filterRef = useRef<HTMLDivElement>(null)
  const submenuInputRef = useRef<HTMLInputElement>(null)

  const activeFiltersCount = filters.roles.length + filters.statuses.length

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (filterRef.current && !filterRef.current.contains(event.target as Node)) {
        setIsFilterOpen(false)
        setActiveSubmenu(null)
        setSubmenuSearch("")
        setMainSearch("")
        setIsSearchingInSubmenu(false)
      }
    }
    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  useEffect(() => {
    function handleEscape(event: KeyboardEvent) {
      if (event.key === "Escape") {
        if (activeSubmenu) {
          setActiveSubmenu(null)
          setSubmenuSearch("")
          setIsSearchingInSubmenu(false)
        } else if (isFilterOpen) {
          setIsFilterOpen(false)
          setMainSearch("")
        } else {
          onFiltersChange({
            search: "",
            roles: [],
            statuses: [],
          })
        }
      }
    }
    document.addEventListener("keydown", handleEscape)
    return () => document.removeEventListener("keydown", handleEscape)
  }, [onFiltersChange, activeSubmenu, isFilterOpen])

  useEffect(() => {
    if (isSearchingInSubmenu && submenuInputRef.current) {
      submenuInputRef.current.focus()
    }
  }, [isSearchingInSubmenu])

  const handleSearchChange = (value: string) => {
    onFiltersChange({ ...filters, search: value })
  }

  const handleFilterToggle = (category: FilterCategory, value: string) => {
    const currentValues = filters[category] as string[]
    const newValues = currentValues.includes(value)
      ? currentValues.filter((v) => v !== value)
      : [...currentValues, value]
    onFiltersChange({ ...filters, [category]: newValues })
  }

  const clearAllFilters = () => {
    onFiltersChange({
      search: "",
      roles: [],
      statuses: [],
    })
  }

  const removeFilter = (category: FilterCategory, value: string) => {
    const currentValues = filters[category] as string[]
    onFiltersChange({
      ...filters,
      [category]: currentValues.filter((v) => v !== value),
    })
  }

  const filterCategories = [
    { key: "roles" as FilterCategory, label: "Роль", icon: User, options: filterOptions.roles },
    {
      key: "statuses" as FilterCategory,
      label: "Статус",
      icon: CheckCircle,
      options: filterOptions.statuses.map((s) => s.value),
    },
  ]

  const filteredCategories = mainSearch
    ? filterCategories.filter((c) => c.label.toLowerCase().includes(mainSearch.toLowerCase()))
    : filterCategories

  const getFilteredOptions = (category: (typeof filterCategories)[0]) => {
    if (!submenuSearch) return category.options
    return category.options.filter((option) => {
      const displayValue =
        category.key === "statuses" ? filterOptions.statuses.find((s) => s.value === option)?.label || option : option
      return displayValue.toLowerCase().includes(submenuSearch.toLowerCase())
    })
  }

  const handleBackClick = () => {
    setActiveSubmenu(null)
    setSubmenuSearch("")
    setIsSearchingInSubmenu(false)
  }

  const handleCloseFilter = () => {
    setIsFilterOpen(false)
    setActiveSubmenu(null)
    setSubmenuSearch("")
    setMainSearch("")
    setIsSearchingInSubmenu(false)
  }

  const handleClearSubmenuSearch = () => {
    setSubmenuSearch("")
    setIsSearchingInSubmenu(false)
  }

  const handleSubmenuHeaderClick = () => {
    setIsSearchingInSubmenu(true)
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center gap-3">
        {/* Поиск */}
        <div className="relative w-72">
          <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
          <Input
            placeholder="Поиск по ФИО или email"
            value={filters.search}
            onChange={(e) => handleSearchChange(e.target.value)}
            className="pl-9 h-10 bg-white"
          />
        </div>

        {/* Дата регистрации */}
        <Popover open={isCalendarOpen} onOpenChange={setIsCalendarOpen}>
          <PopoverTrigger asChild>
            <Button variant="outline" className="h-10 gap-2 bg-white">
              <Calendar className="h-4 w-4" />
              <span className="text-muted-foreground">Дата регистрации</span>
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto p-0" align="start">
            <CalendarComponent
              mode="single"
              selected={filters.registrationDate}
              onSelect={(date) => {
                onFiltersChange({
                  ...filters,
                  registrationDate: date,
                })
              }}
            />
          </PopoverContent>
        </Popover>

        {/* Фильтр */}
        <div className="relative" ref={filterRef}>
          <Button
            variant={activeFiltersCount > 0 ? "default" : "outline"}
            className={cn("h-10 gap-2", activeFiltersCount > 0 ? "bg-foreground text-background" : "bg-white")}
            onClick={() => {
              setIsFilterOpen(!isFilterOpen)
              if (isFilterOpen) {
                setActiveSubmenu(null)
                setSubmenuSearch("")
                setMainSearch("")
                setIsSearchingInSubmenu(false)
              }
            }}
          >
            <SlidersHorizontal className="h-4 w-4" />
            Фильтр
            {activeFiltersCount > 0 && (
              <span className="ml-1 flex h-5 w-5 items-center justify-center rounded-full bg-orange-500 text-xs text-white">
                {activeFiltersCount}
              </span>
            )}
          </Button>

          {isFilterOpen && (
            <div className="absolute top-full left-0 mt-2 w-72 rounded-xl border border-gray-200 bg-white shadow-lg z-50 overflow-hidden">
              {!activeSubmenu ? (
                <div>
                  {/* Поиск сверху без рамки */}
                  <div className="flex items-center gap-2 px-4 py-3 border-b border-gray-100">
                    <Search className="h-4 w-4 text-gray-400 flex-shrink-0" />
                    <input
                      type="text"
                      placeholder="Фильтр..."
                      value={mainSearch}
                      onChange={(e) => setMainSearch(e.target.value)}
                      className="flex-1 text-sm bg-transparent outline-none placeholder:text-gray-400"
                    />
                  </div>
                  {/* Список категорий */}
                  <div className="py-1">
                    {filteredCategories.length > 0 ? (
                      filteredCategories.map((category) => (
                        <button
                          key={category.key}
                          className="w-full flex items-center gap-3 px-4 py-2.5 text-sm hover:bg-gray-50 transition-colors text-left"
                          onClick={() => {
                            setActiveSubmenu(category.key)
                            setMainSearch("")
                          }}
                        >
                          <category.icon className="h-4 w-4 text-gray-400 flex-shrink-0" />
                          <span className="flex-1 text-gray-900">{category.label}</span>
                          <ChevronRight className="h-4 w-4 text-gray-300" />
                        </button>
                      ))
                    ) : (
                      <div className="px-4 py-8 text-center text-sm text-gray-500">Ничего не найдено</div>
                    )}
                  </div>
                </div>
              ) : (
                <div>
                  {/* Заголовок подменю */}
                  <div className="flex items-center gap-2 px-3 py-3 border-b border-gray-100">
                    <button
                      onClick={handleBackClick}
                      className="p-1 hover:bg-gray-100 rounded transition-colors flex-shrink-0"
                    >
                      <ArrowLeft className="h-4 w-4 text-gray-600" />
                    </button>

                    {isSearchingInSubmenu || submenuSearch ? (
                      <>
                        <input
                          ref={submenuInputRef}
                          type="text"
                          value={submenuSearch}
                          onChange={(e) => setSubmenuSearch(e.target.value)}
                          placeholder=""
                          className="flex-1 text-sm bg-transparent outline-none"
                          autoFocus
                        />
                        <button
                          onClick={handleClearSubmenuSearch}
                          className="p-1 hover:bg-gray-100 rounded transition-colors flex-shrink-0"
                        >
                          <X className="h-4 w-4 text-gray-400" />
                        </button>
                      </>
                    ) : (
                      <>
                        <button onClick={handleSubmenuHeaderClick} className="flex-1 text-left">
                          <span className="text-sm text-gray-400">|</span>
                          <span className="text-sm text-gray-900 ml-0.5">
                            {filterCategories.find((c) => c.key === activeSubmenu)?.label}
                          </span>
                        </button>
                        <button
                          onClick={handleCloseFilter}
                          className="p-1 hover:bg-gray-100 rounded transition-colors flex-shrink-0"
                        >
                          <X className="h-4 w-4 text-gray-400" />
                        </button>
                      </>
                    )}
                  </div>

                  {/* Список опций */}
                  <div className="py-1 max-h-72 overflow-y-auto">
                    {(() => {
                      const category = filterCategories.find((c) => c.key === activeSubmenu)!
                      const filteredOptions = getFilteredOptions(category)

                      if (filteredOptions.length === 0) {
                        return <div className="px-4 py-8 text-center text-sm text-gray-400">Ничего не найдено</div>
                      }

                      return filteredOptions.map((option) => {
                        const displayValue =
                          category.key === "statuses"
                            ? filterOptions.statuses.find((s) => s.value === option)?.label || option
                            : option
                        const isChecked = (filters[category.key] as string[]).includes(option)

                        return (
                          <button
                            key={option}
                            className="w-full flex items-center gap-3 px-4 py-2.5 text-sm hover:bg-gray-50 transition-colors text-left"
                            onClick={() => handleFilterToggle(category.key, option)}
                          >
                            <span className="flex-1 text-gray-900 truncate">{displayValue}</span>
                            {isChecked && <Check className="h-4 w-4 text-blue-500 flex-shrink-0" />}
                          </button>
                        )
                      })
                    })()}
                  </div>
                </div>
              )}
            </div>
          )}
        </div>

        {activeFiltersCount > 0 && (
          <button
            onClick={clearAllFilters}
            className="text-sm text-muted-foreground hover:text-foreground flex items-center gap-1"
          >
            Очистить
            <span className="text-xs px-1.5 py-0.5 bg-muted rounded">ESC</span>
          </button>
        )}
      </div>

      {/* Активные фильтры */}
      {activeFiltersCount > 0 && (
        <div className="flex flex-wrap items-center gap-2">
          {filters.roles.map((r) => (
            <span key={r} className="inline-flex items-center gap-1.5 px-3 py-1.5 bg-white border rounded-full text-sm">
              <User className="h-3.5 w-3.5 text-muted-foreground" />
              Роль: {r}
              <button onClick={() => removeFilter("roles", r)} className="ml-1 hover:text-foreground">
                <X className="h-3.5 w-3.5" />
              </button>
            </span>
          ))}
          {filters.statuses.map((s) => (
            <span key={s} className="inline-flex items-center gap-1.5 px-3 py-1.5 bg-white border rounded-full text-sm">
              <CheckCircle className="h-3.5 w-3.5 text-muted-foreground" />
              Статус: {filterOptions.statuses.find((st) => st.value === s)?.label}
              <button onClick={() => removeFilter("statuses", s)} className="ml-1 hover:text-foreground">
                <X className="h-3.5 w-3.5" />
              </button>
            </span>
          ))}
        </div>
      )}
    </div>
  )
}

